/* eslint-disable jsx-a11y/anchor-is-valid */
import React from 'react';
import { Col, Row } from 'react-styled-flexboxgrid'
import styled from 'styled-components';

const OtherMatch = styled.a`
text-decoration: ${(props) => props.selected ? 'underline' : ''};
 font-weight: ${(props) => props.selected ? 700 : 'normal'};
`;
const Description = styled.div`
margin-top: 24px;
p{
  width: 205px;
  height: 160px;
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 8;
}
`;
const Match = ({ currentMatch, matches, updateMatch }) => {
  const synonymLength = currentMatch && currentMatch.synonyms && currentMatch.synonyms.length;

  return (
    <Row>
      <Col xs={8}>
        <h2>Current Match</h2>
        <Row>
          <Col xs={6}>
            <img
              style={{ maxWidth: 220 }}
              src={currentMatch.coverImage.extraLarge || currentMatch.coverImage.large || currentMatch.coverImage.medium}
              alt={currentMatch.title.english || currentMatch.title.romaji || currentMatch.title.romaji}
            />
          </Col>
          <Col xs={6}>
            <div>
              <h3>Title</h3>
              <p>English: {currentMatch.title.english}</p>
              <p>Romaji: {currentMatch.title.romaji}</p>
              <p>Native: {currentMatch.title.romaji}</p>
              <p>Synonyms:&nbsp;
              {currentMatch.synonyms.map((synonym, index) => (
                <span>
                  {synonym}
                  {index < synonymLength - 1 && ','}
                </span>
              ))}
              </p>
            </div>

            <Description>
              <h3>Description</h3>
              <p>{currentMatch.description && currentMatch.description.replace(/<br>/gi, '\n').replace(/<b>|<\/b>|<i>|<\/i>/gi, '')}</p>
            </Description>
          </Col>
        </Row>
      </Col>
      {matches && matches.length > 0 &&
        <Col xs={4}>
          <h2>Other Matches:</h2>
          <ul>
            {matches.map((match) => (
              <li>
                <OtherMatch onClick={() => { updateMatch(match) }}
                  selected={match.id === currentMatch.id}>
                  {match.title.english || match.title.romaji || match.title.romaji}
                </OtherMatch>
              </li>
            ))}
          </ul>
        </Col>
      }
    </Row>
  )
};
export default Match;