import React from 'react';
import styled from 'styled-components';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';

import Metadata from './Metadata';
import Icon from './Icon'
import { getCookie, fetchAnilistData } from './helpers';

function App() {
  const [open, setOpen] = React.useState(false);
  const [isFetching, setIsFetching] = React.useState(false);
  const [anilistData, setAnilistData] = React.useState({});
  const prefersDarkMode = useMediaQuery('(prefers-color-scheme: dark)') || getCookie('darkmode') === 'true';

  const theme = React.useMemo(
    () =>
      createMuiTheme({
        palette: {
          type: prefersDarkMode ? 'dark' : 'light',
        },
      }),
    [prefersDarkMode],
  );

  const anilistCallback = (data) => {
    setIsFetching(false);
    setAnilistData(data);
    setOpen(true);
  }

  const fetchData = () => {
    const titleElement = document.querySelector('.v-main__wrap .v-toolbar__content .v-toolbar__title span')
    const title = titleElement && titleElement.innerText;// a.innerText
    if(title){
      setIsFetching(true);
      fetchAnilistData(title, anilistCallback);
    }
  };

  const onClose = () => setOpen(false);

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <div className="App">
        <button
          type="button"
          className="v-btn v-btn--flat v-btn--icon v-btn--round theme--dark v-size--default"
          onClick={fetchData}
        >
          <Icon aria-hidden="true" />
        </button>
        {isFetching &&
          <p>Loading....</p>
        }
        <Metadata
          open={open}
          onClose={onClose}
          anilistData={anilistData}
          setIsFetching={setIsFetching}
          setAnilistData={setAnilistData}
        />
      </div>
    </ThemeProvider>

  );
}

export default App;
