import React from 'react';
import { useForm, Controller } from 'react-hook-form';
import { TextField as MUITextField } from "@material-ui/core";
import styled from 'styled-components';
import Button from '@material-ui/core/Button';

const TextField = styled(MUITextField)`
margin: 12px auto;
display: flex;
`
const MetadataModal = (props) => {

  const { readOnly, defaultValues, onSubmit, setReset } = props;

  const { control, handleSubmit, errors, reset  } = useForm({ defaultValues });

  console.log(errors);

    setReset(reset)

  return (
    <div>
      <form onSubmit={handleSubmit(onSubmit)}>
        <Controller
          as={TextField}
          name="title"
          label="Title"
          placeholder="Title"
          control={control}
          
          variant="filled"
          InputProps={{
            readOnly: readOnly.title,
          }}

        />

        <Controller
          as={TextField}
          name="sortTitle"
          label="Sort Title"
          placeholder="Sort Title"
          control={control}
          
          variant="filled"
          InputProps={{
            readOnly: readOnly.sortTitle,
          }}

        />

        <Controller
          as={TextField}
          name="summary"
          label="Summary"
          placeholder="Summary"
          control={control}
          
          variant="filled"
          multiline
          InputProps={{
            readOnly: readOnly.summary,
          }}

        />

        <Controller
          as={TextField}
          name="status"
          label="Status"
          placeholder="Status"
          control={control}
          
          variant="filled"
          InputProps={{
            readOnly: readOnly.status,
          }}

        />

        <Controller
          as={TextField}
          name="publisher"
          label="Publisher"
          placeholder="Publisher"
          control={control}
          
          variant="filled"
          InputProps={{
            readOnly: readOnly.publisher,
          }}

        />

        <Controller
          as={TextField}
          name="genres"
          label="Genres"
          placeholder="Genres"
          control={control}
          
          variant="filled"
          InputProps={{
            readOnly: readOnly.genres,
          }}

        />

        <Controller
          as={TextField}
          name="tags"
          label="Tags"
          placeholder="Tags"
          control={control}
          
          variant="filled"
          InputProps={{
            readOnly: readOnly.tags,
          }}

        />
        <Button style={{ float: 'right' }} type="submit">Save Changes</Button>
      </form>
    </div>
  );
}

export default MetadataModal;
